@import '../../variables.scss';

.menu{
    &-button{
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        background-color: transparent;
        border: none;
        cursor: pointer;
        span{
            background-color: $white;
            width: 15px;
            height: 2px;
            display: block;
            margin-bottom: 10px;
            transition: width 300ms ease-in-out;
            @include tablet{
                width: 25px;
            }
            &:nth-child(1), &:nth-child(3){
                width:30px;
                @include tablet{
                    width: 40px;
                }
            }
            &:nth-child(3){
                margin-bottom: 0;
            }
        }
        &:hover{
            span{
                width:40px;
                &:nth-child(1), &:nth-child(3){
                    width:25px;
                }
            }
        }
        @include tablet{

        }
    }
    &-close{
        background-color: transparent;
        border: none;
        cursor: pointer;
        padding: 0;
        position: absolute;
        top:1.5rem;
        right: 5%;
        width: 20px;
    }
}


.main-nav{
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    background-color: rgba($black,0.98);
    transform:scale(1);
    transition: all 300ms ease-in-out;
    opacity: 1;

    nav{
        display: flex;
        flex-direction: column;
    }
    a{
        text-decoration: none;
        color:$white;
        font-family: $heading-font;
        font-size: 3rem;
        transition: all 250ms ease-in-out;
        animation: 600ms ease-in-out fadeInRight;

        @include tablet{
            font-size: 4rem;
        }
        &:nth-child(1){
            animation-delay: 200ms;
        }
        &:nth-child(2){
            animation-delay: 400ms;
        }
        &:nth-child(3){
            animation-delay: 600ms;
        }
        &:nth-child(4){
            animation-delay: 800ms;
        }
        &:nth-child(5){
            animation-delay: 1000ms;
        }
        span{
            color:$primary;
            margin-right: 1rem;
        }
        &:hover{
            color:$primary;
        }
    }
}

.nav-closed{
    transform:scale(0);
    opacity: 0;
}

@keyframes fadeInRight {
    0% {
        transform: translateX(-20%);
        opacity: 0;
    }
    100%{
        transform: translateX(0);
        opacity: 1;
    }
}